      padding-bottom: 7px;
      text-transform: uppercase;
    }

    .contact-info {
      background: #fff;
      border-radius: 10px;
      padding: 10px 16px;
      margin-bottom: 30px;

      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 0;

        .value {
          font-weight: 500;
          padding-left: 8px;
        }
      }
    }
